import { Injectable } from '@angular/core';
import { HttpClient} from '@angular/common/http';
import { Observable } from 'rxjs';
import { Event } from './event';
import { Userclazz } from '../userclazz';
import { Allevent } from './allevent';

@Injectable({
  providedIn: 'root'
})
export class EventService {

  constructor(public http: HttpClient) { }

  findAlleventsbyevent(): Observable<Event[]>  {
    let event = new Event();
    let allevent = new Allevent();
    allevent.idevent = Number(sessionStorage.getItem('idevent')); 

    event.allevent = allevent;
    return this.http.post<any>('/eventsbyevent', event)
  }


  findAlleventsbyuser(): Observable<Event[]>  {
    let event = new Event();
    let userclazz = new Userclazz();
    userclazz.iduser = Number(sessionStorage.getItem('iduser')); 

    event.userclazz = userclazz;
    return this.http.post<any>('/eventsbyuser', event)
  }


  saveevents(event: Event): Observable<Event[]> {
    let userclazz = new Userclazz();
    userclazz.iduser = Number(sessionStorage.getItem('iduser'));
 
    let allevent = new Allevent();
    allevent.idevent = Number(sessionStorage.getItem('idevent')); 

    event.userclazz = userclazz;
    event.allevent = allevent;

    return this.http.post<any>("/event", event)
  }

  logoutevent(): Observable<Event[]> {
    let event = new Event();
    let userclazz = new Userclazz();
    userclazz.iduser = Number(sessionStorage.getItem('iduser'));
 
    let allevent = new Allevent();
    allevent.idevent = Number(sessionStorage.getItem('idevent')); 

    event.userclazz = userclazz;
    event.allevent = allevent;
    return this.http.post<any>('/logoutevent', event)
  }

  deleteaccount(): Observable<Event[]> {
    let event = new Event();
    let userclazz = new Userclazz();
    userclazz.iduser = Number(sessionStorage.getItem('iduser'));

    event.userclazz = userclazz;
    return this.http.post<any>('/deleteaccount', event)
  }

  deleteevent(): Observable<Event[]> {
    let event = new Event();
 
    let allevent = new Allevent();
    allevent.idevent = Number(sessionStorage.getItem('idevent')); 

    event.allevent = allevent;
    return this.http.post<any>('/deleteevent', event)
  }

  deleteevents(id) {
    return this.http.delete('/event/' + id)
  }





  // findbyevent(event: Event): Observable<Event[]> {
  //   let allevent = new Allevent();
  //   allevent.idevent = Number(sessionStorage.getItem('idevent')); 
  //   event.allevent = allevent;
  //   return this.http.post<any>("http://localhost:8080/eventsbyevent", event)
  // }


}